.header-social_box-switch-item {
  position: relative;
  list-style-type: none;
  font-family: Rubik-Medium;
  font-size: 14px;
  line-height: 17px;
}

.header-social_box-switch-item::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 200%;
  top: 95%;
  background: transparent;
}

.header-social_box-switch-items {
  padding: 0px;
  margin: 0px;
}

.header-social_box-switch-item a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #4F4F4F;
}

.header-social_box-switch-item a img {
  margin-right: 9px;
}

.header-social_box-switch-item:hover .header-social_box-switch-items-dropbox {
  display: flex;
  animation: navups 300ms ease both;
}

.header-social_box-switch-items-dropbox {
  display: none;
  justify-content: space-around;
  padding: 10px;
  margin: 0px;
  position: absolute;
  left: -25%;
  top: 150%;
  height: 79px;
  width: 74px;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border-radius: 15px;
  background-color: #FFFFFF;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}

@keyframes navups {
  0% {
    transform: translateY(100%);
    opacity: .1;
  }

  50% {
    opacity: .5;
  }

  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

.header-nav_page-links {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.header-nav_page-links-items {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 0px;
  margin: 0px;
}

.header-nav_page-link-item:first-child {
