}

.header-nav_page-link-item {
  list-style: none;
  width: 100%;
  text-align: center;
}

.header-nav_page-link-item > a {
  position: relative;
  color: #4F4F4F;
  font-family: Rubik-Medium;
  font-size: 16px;
  line-height: 19px;
  transition: all 300ms;
  text-decoration: none;
}

.header-nav_page-link-item > a::before {
  content: "";
  transition: all 300ms;
}

.header-nav_page-link-item > a::after {
  content: "";
  position: absolute;
  border: 4.5px solid transparent;
  border-bottom: 9px solid transparent;
  bottom: -12px;
  left: 47.5%;
  transition: all 300ms;
}

.header-nav_page-link-item > a:hover {
  color: #3D7EBA;
}

.header-nav_page-link-item > a:hover:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background: #3D7EBA;
  bottom: -12px;
}

.header-nav_page-link-item > a:hover::after {
  content: "";
  position: absolute;
  border: 4.5px solid transparent;
  border-bottom: 9px solid #3D7EBA;
  bottom: -12px;
  left: 47.5%;
}

.header-nav_page-link-item .header-nav_page-link-item-active {
  position: relative;
  color: #3D7EBA;
}

.header-nav_page-link-item .header-nav_page-link-item-active:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  background: #3D7EBA;
  bottom: -12px;
}

.header-nav_page-link-item .header-nav_page-link-item-active::after {
  content: "";
  position: absolute;
  border: 4.5px solid transparent;
  border-bottom: 9px solid #3D7EBA;
  bottom: -12px;
  left: 47.5%;
}

