  flex-flow: column;
  transition: all 300ms;
}

.faq_wrapper .fq-items dl > dt {
  border: 1px solid #E3E3E3;
  margin-bottom: -1px;
}

.faq_wrapper .fq-items dl.active dt {
  border-color: transparent;
  background: inherit;
  color: #fff;
}

.faq_wrapper .fq-items dl:last-child > dt {
  margin-bottom: 0px;
}

