}

.faq_wrapper .fq-items .faq-toggle {
  margin-right: 20px;
  margin-top: 20px;
  position: absolute;
  line-height: 1em;
  text-align: center;
  color: #a677c3;
  font-weight: 700;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  display: table;
  cursor: pointer;
  transition: all 300ms;
}

.faq_wrapper .fq-items .faq-toggle svg {
  transform: rotateZ(90deg);
}

.faq_wrapper .fq-items .faq-toggle svg g g {
  fill: #4F4F4F;
}

.faq_wrapper .fq-items dd {
  padding: 35px 50px;
  margin-top: -10px;
  order: 2;
  background: #fff;
}

.faq_wrapper .fq-items dl.active {
  background: #3D7EBA;
}

.faq_wrapper .fq-items dl.active .faq-toggle {
  margin-right: 25px;
  margin-top: 13px;
}

